
.description{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.view {
    width: 100%;
    height: auto;
}

.description h2 {
    font-size: 1rem;
    margin: 3rem 3rem;
    text-align: left;

}

@media screen and (min-width: 1024px) {
    
    .view-wrapper {
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        
    }

    .view-wrapper h2 {
        width: 50%;
        margin: 2rem 0;
        padding: 0rem 4rem;
        text-align: center;
        font-size: 1.2rem;
        font-weight: 400;
    }

    .view {
        width: 50%;
        height: auto;
    }

    .view-wrapper-reverse {
        width: 100%;
        display: flex;
        flex-direction: row-reverse;
        justify-content: center;
        align-items: center;
    }
    
    .view-wrapper-reverse h2 {
        width: 50%;
        margin: 2rem 0;
        padding: 0rem 4rem;
        font-size: 1.2rem;
        font-weight: 400;
        text-align: center;
    }
}