@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;500;600;700;800&family=Poppins:wght@300;400&display=swap);
:root{--navbar: rgb(114, 82, 39);--dropdown: rgb(78, 67, 54);--background: rgb(255, 254, 254);--navFontColor: rgb(248, 247, 244);--linkFontColor: rgb(248, 247, 244)}*{margin:0;padding:0;box-sizing:border-box;text-decoration:none;list-style:none}body{background-color:rgb(255, 254, 254);background-color:var(--background);font-family:'Open Sans', sans-serif;font-family:'Poppins', sans-serif}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

nav {
    background-color: rgb(17, 17, 17);
    background-color: var(--navbar);
    padding: 1rem 1rem;
    display: flex;
    justify-content: space-around;
    align-items: center;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    width: 100%;
    height: 5rem;
   
}

.logo a {
    color: var(--navFontColor);
}

.logo {
    text-align: left;
    width: 60%;
    font-size: 1.2rem;
}

.menu-icon {
    display: flex;
    flex-direction: column;
    cursor: pointer;
}


i {
    background-color: var(--navFontColor);
    width: 25px;
    height: 2px;
    margin: 2px;
    border-radius: 2px;
    z-index: 10;
    transition: all ease 0.5s; 
}

.open:nth-child(1){
    -webkit-transform: rotate(45deg) translateY(9px);
            transform: rotate(45deg) translateY(9px);
}

.open:nth-child(2){
    opacity: 0;
}

.open:nth-child(3){
    -webkit-transform: rotate(-45deg) translateY(-9px);
            transform: rotate(-45deg) translateY(-9px);
}

.close:nth-child(1){
    -webkit-transform: rotate(0) translateY(0);
            transform: rotate(0) translateY(0);
}

.close:nth-child(2){
    opacity: 1;
}

.close:nth-child(3){
    -webkit-transform: rotate(0) translateY(0);
            transform: rotate(0) translateY(0);
}

.links { 
    display: none;
}

.nav-links-closed{
    position: absolute;
    background-color: var(--dropdown);
    top: 4.9rem;
    width: 100%;
    height: 0vh;
    transition: all 0.2s ease;
    overflow: hidden;
}

.nav-links-closed a {
    color: var(--linkFontColor);
}

.nav-links-closed ul {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.nav-links-closed ul li {
    margin: 3rem 0;
    font-size: 1.2rem;
    font-weight: 500;
    opacity: 0;
}

.nav-links {
    position: absolute;
    background-color: var(--dropdown);
    top: 4.9rem;
    width: 100%;
    height: 29rem;
    transition: all .5s ease;
}

.nav-links a {
    color: var(--linkFontColor);
}

.nav-links ul {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.nav-links ul li {
    margin: 2rem 0;
    font-size: 1.2rem;
    font-weight: 500;
    opacity: 1;
    transition: opacity .5s ease 0.1s;
}

@media screen and (min-width: 768px) {

    nav {
        padding: 1rem 1rem;
    }

    .logo {
        font-size: 1.4rem;
    }

    .menu-icon {
        display: none;
    }

    .nav-links-closed {
        display: none;
    }

    .nav-links {
        display: none;
    }

    .links {
        display: flex;
        width: 80%;
    }

    .links a {
        color: var(--navFontColor);
        
    }

    .links ul {
        width: 100%;
        display: flex;
        justify-content: flex-end;
        align-items: center;
        
    }

    .links ul li {
        width: -webkit-fit-content;
        width: -moz-fit-content;
        width: fit-content;
        padding-left: 1rem;
        font-weight: 500;
    }

    .links a:hover {
        color:rgb(37, 31, 24)
    }
}

@media screen and (min-width: 1024px){

    nav {
        padding: 1rem 2rem;
    }

    .links ul {
        text-align: center;
    }

    .links ul li {
        padding-left: 2rem;
        font-weight: 500;
        font-size: 1.1rem;
    }
}

.img-container{background-image:url("/duplex-val-thorens/static/media/val thores3.2287afc9.jpg");background-repeat:no-repeat;background-size:cover;background-position:left;width:100%;height:25rem;display:flex;justify-content:center;align-items:center}.welcome{background-color:var(--dropdown);font-size:1.2rem;padding:3rem 1rem;color:white;font-weight:400}@media screen and (min-width: 1024px){.img-container{height:35rem}.welcome{font-size:1.4rem;padding:3rem 7rem}}


.description{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.view {
    width: 100%;
    height: auto;
}

.description h2 {
    font-size: 1rem;
    margin: 3rem 3rem;
    text-align: left;

}

@media screen and (min-width: 1024px) {
    
    .view-wrapper {
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        
    }

    .view-wrapper h2 {
        width: 50%;
        margin: 2rem 0;
        padding: 0rem 4rem;
        text-align: center;
        font-size: 1.2rem;
        font-weight: 400;
    }

    .view {
        width: 50%;
        height: auto;
    }

    .view-wrapper-reverse {
        width: 100%;
        display: flex;
        flex-direction: row-reverse;
        justify-content: center;
        align-items: center;
    }
    
    .view-wrapper-reverse h2 {
        width: 50%;
        margin: 2rem 0;
        padding: 0rem 4rem;
        font-size: 1.2rem;
        font-weight: 400;
        text-align: center;
    }
}
